events.on('ready', function() {
    $('.botao').on('click', function(){
        $(this).removeClass('infinite');
    });

    $('.flip-click').on('click', function(){
        $(this).removeClass('infinite');
    });

    $('video').attr('tabindex', '-1')

    $('.owl-next').addClass('animated pulse infinite');

    $('.line-interation').find('.line-major').addClass('animated fadeInRight');
    $('.line-interation').find('.botao').on('click', function(){
        $(this).parent().parent('.line-minor').siblings('.line-major').css('display','block');
    })

    $('.sec01').isInViewportComplete({
        container: window, 
        released: true,
        call: function() {
            $('.op01, .op02, .op03, .op04').removeClass('active')
        }
    });
    $('.parentPonto1').isInViewportComplete({
        container: window, 
        released: true,
        call: function() {
            $('.op01').addClass('active');
            $('.op02, .op03, .op04').removeClass('active')
            // console.log('teste1')
        }
    });
    $('.parentPonto2').isInViewportComplete({
        container: window,
        released: true,
        call: function() {
            $('.op02').addClass('active');
            $('.op01, .op03, .op04').removeClass('active')
            // console.log('teste2')
        }
    });
    $('.parentPonto3').isInViewportComplete({
        container: window,
        released: true,
        call: function() {
            $('.op03').addClass('active');
            $('.op01, .op02, .op04').removeClass('active')
            // console.log('teste3')
        }
    });
    $('.parentPonto4').isInViewportComplete({
        container: window,
        released: true,
        call: function() {
            $('.op04').addClass('active');
            $('.op01, .op02, .op03').removeClass('active')
            // console.log('teste3')
        }
    });

});